@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
.cart {
    min-height: 78vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.cart h2 {
    margin: 2rem 0 1rem 0;
}

.cart-details {
    display: flex;
    margin: 2rem 0;
}

.cart-item {
    width: 38rem;
    margin-bottom: 1rem;
    padding: 1rem;
    border: 1px solid #ccc;
}

.cart-item h3 {
    margin-bottom: 1rem;
}

p {
    padding: 0.5rem 0;
}

.cart-actions {
    width: 8rem;
    display: flex;
    flex-direction: column;
}

.qty {
    width: 3rem;
    margin-left: 0.5rem;
}

.cart-summary {
    margin-left: 1rem;
}

.cart-summary  h4 {
    margin-bottom: 1rem;
    font-weight: bold;
}

.product {
    max-width: 80vw;
    display: flex;
    margin: 2rem auto;
    padding: 1rem;
    background-color: #f3f4f7;
    box-shadow: 1px 3px 9px 2px rgba(0,0,0,0.25);
}

.products h2 {
    padding-top: 2rem;
}

.product-info {
    width: 30rem;
}

.product-btns {
    align-self: end;
    margin-left: auto;
}

.product-img {
    min-width: 150px;
    height: 180px;
    margin: 0 1rem 1rem 1rem;
    object-fit: contain;
}

/* .product-img img {
    width: 100%;
} */

.product h3 {
    padding-bottom: 1.5rem;
}

.price {
    margin-top: 1rem;
}

/* single product */
.single-cart-product-container {
    height: 75vh;
}

.single-cart-product-container h2 {
    margin-top: 3rem;
}

.single-cart-product {
    width: 58rem;
    margin: 3rem auto;
}

.single-cart-product img {
    margin: 1rem;
    margin-top: 0;
}

.single-cart-product .price {
    margin-top: 0rem;
}

.add-cart {
    margin-top: 0.9rem;
} 

.auth {
    margin: 3rem auto;
    height: 68vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

form {
    width: 30rem;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

form fieldset {
    width: 20rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem auto;
    padding: 1rem;
    border-radius: 0.3em;
}

form label {
    display: flex;
    flex-direction: column;
}

label input {
    margin: 0.5rem 0;
}

.auth-user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.5em;
}

.auth-user h3 {
    padding: 2rem 0 1rem 0
}

.login-btn {
    background-color: #c6e4c6;
    margin-top: 1rem;
    padding: 0.5rem 2rem;
}

.logout-btn {
    background-color: #e7c9ab;
    margin-top: 2rem;
}

nav {
    padding: 1rem 0;
    background-color: #8d93ab;
    box-shadow: 1px 5px 9px 2px rgba(0,0,0,0.25);
}

nav ul  {
    /* width: 100vw; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}

nav ul li {
    list-style: none;
}

nav ul a {
    margin: 0 1rem;
    text-decoration: none;
    font-weight: bold;
    color: #393b44;
}

.active {
    color: #d6e0f0;
}

.logo {
    color: #f1f3f8 !important;
}

.nav-link-cart {
    display: flex;
}

.nav-link-cart p {
    align-self: start;
    padding: 0 0.5rem;
    align-self: start;
}

.nav-link-cart p:first-child {
    padding: 0;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    font-family: 'Ubuntu', sans-serif;
    letter-spacing: 0.03em;
    color: #f1f3f8;
    background-color: #393b44;
    box-shadow: 1px 0px 10px 2px rgba(0,0,0,0.25);
}

* {
    margin: 0;
}

body {
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #393b44;
  background-color: #f1f3f8;
}

.app h2 {
  text-align: center;
}

/* HOME */
.home {
  height: 84vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* BUTTONS */
.btns {
  margin-right: 0.3em;
  padding: 0.5rem;
  color: #393b44;
  border: 1px solid #393b44;
  border-radius: 0.2rem;
}

.btns:hover {
  box-shadow: 1px 3px 9px 2px rgba(0,0,0,0.25);
}

.add {
  background-color: #d6e0f0;
}

/* .view {
    background-color: #d6e0f0;
    background-color: #8d93ab;
} */

.remove {
  background-color: #e7c9ab;
  margin-top: 1rem;
}

.proceed {
  background-color: #c6e4c6;
  margin-top: 1rem;
}

.bold {
    font-weight: bold;
}

/* NOT FOUND */
.not-found {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

